<template>
  <div class="list">
    <myhead/>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item,index) in wangbaArr.img" :key="index">
         <img :src="item.src" alt="" class="imgSrc">
        </div>
      </div>
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
      <div class="swiper-pagination"></div>
    </div>
    <div class="connenct">
      <h3 class="name">{{wangbaArr.name}}</h3>
      <p  class="comment">:{{wangbaArr.comment}}</p>
      <span class="price">:{{wangbaArr.price}}</span>
      <span class="member">:{{wangbaArr.member}}</span>
      <p class="text"><span class="pinglun">:</span>{{wangbaArr.text}}</p>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import mock from '@/mock'
  import Swiper from 'Swiper'
  import myhead from '@/components/myhead'
  export default {
    data(){
      return {
        wangbaArr:""
      }
    },
    components:{
      myhead
    },
    mounted(){
      this.init()
    },
    methods:{
      init(){
        localStorage.index = this.$route.params.index
        localStorage.name = this.$route.params.name
        let index = this.$route.params.index || localStorage.index
        let name = this.$route.params.name || localStorage.name
        axios.get('/api/wangbadetail').then(res=>{
          if(res.data[index].name == name){
            this.wangbaArr = res.data[index]
          }
        })
        setTimeout(()=>{
          var mySwiper = new Swiper ('.swiper-container', {
            direction: 'horizontal', // 
            loop: true, // 
            
            // 
            pagination : '.swiper-pagination',
            paginationType : 'progress',
            
            // 
            prevButton:'.swiper-button-prev',
            nextButton:'.swiper-button-next',
          })  
        },100)
      }
    }
  }
</script>

<style scoped>
.list{
  width:100%;
  height:100%;
  box-sizing: content-box;
}
.list .swiper-container{
  width: 100%;
}
.list .swiper-wrapper{
  width:700%;
  display: flex;
}
.list .swiper-wrapper .swiper-slide{
  width:37.5rem;
  height:20rem;
}
.list img{
  width:100%;
  height:100%;
}
.connenct{
  width: 100%;
  height:100%;
  padding:0 1rem;
  box-sizing: border-box;
}
.name{
  font-size:1.8rem;
  padding-top:1rem;
}
.pinglun{
  font-size:1.4rem;
  color:rgb(139, 136, 139);
  display: block;
  padding-bottom: 1rem;
}
.text{
  font-size:1.4rem;
  padding:1rem 0;
  color:rgb(139, 136, 139);
}
.price{
  font-size:1.3rem;
  color:red;
  display: block;
  padding:1rem 0;
}
.member{
  font-size:1.3rem;
  color:blue;
  display: block;
}
.comment{
  font-size:1.5rem;
  color:chocolate;
  font-weight: bold;
  padding:1rem 0;
}
</style>


